import React from "react";
import { HStack, VStack, Text, Box } from "native-base";
import Util from "../util";
import WhiteLogo from "./svg/WhiteLogo";
import bg from "../assets/bg.png";
const { vw, vh } = Util;

const LogoBlock = () => {
  return (
    <Box
      style={{
        justifyContent: "center",
        width: vw(600),
        flex:1,
        background:
          "transparent linear-gradient(260deg, rgba(186, 71, 243, 1) 0%, rgba(125, 43, 233, 1) 100%) 0% 0% no-repeat padding-box",
      }}
    >
      <HStack justifyContent={"center"} space={4}>
        <WhiteLogo
          width={vw(43)}
          // height={hp(6.0293)}
        />
        <VStack height={vh(81)}>
          <Text
            _web={{
              height: vh(20.5),
              fontSize: vw(32),
              color: "white",
              fontFamily: "Poppins_medium",
              textShadowColor: "rgba(0, 0, 0, 1)",
              textShadowOffset: { width: 0, height: 5 },
              textShadowRadius: 20,
              marginBottom: vh(15),
            }}
          >
            Shadow
          </Text>
          <Text
            _web={{
              height: vh(20.5),
              fontSize: vw(32),
              color: "white",
              fontFamily: "Poppins_medium",
              textShadowColor: "rgba(0, 0, 0, 1)",
              textShadowOffset: { width: 0, height: 5 },
              textShadowRadius: 20,
            }}
          >
            Properties
          </Text>
        </VStack>
      </HStack>
    </Box>
  );
};

export default LogoBlock;
